// header styles
header {
    background-color: rgba(34, 49, 52, 0.9);
    height: 65px;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 10;
    .header-left {
        float: left;
        .logo {
            margin-top: 20px;
            width: 124px;
        }
    }
    .header-right {
        float: right;
        transition: all 0.5s;
        &:hover {
            background-color: rgba(255, 255, 255, 0.5);
        }
        a {
            color: #fff;
            display: block;
            line-height: 65px;
            padding-left: 25px;
            padding-right: 25px;
        }
    }
}

//top modules
.container {
    margin-left: auto;
    margin-right: auto;
    padding-left: 15px;
    padding-right: 15px;
    width: 1170px;

    .btn-wrapper {
        margin-top: 10px;
        margin-bottom: 10px;
        p {
            margin-top: 10px;
            margin-bottom: 10px;
        }
    }

    .btn {
        border-radius: 4px;
        color: #fff;
        display: inline-block;
        padding: 12px 24px;
        opacity: 0.8;
        &:hover {
            opacity: 1;
        }
    }

    .fa {
        margin-right: 5px;
    }    
    .singup {
        background-color: #239b76;
    }

    .facebook {
        background-color: #3b5998;
        margin-right: 10px;
    }
    .twitter {
        background-color: #55acee;
    }
}

//lessons tyles
.lesson-wrapper {
    background-color: #f7f7f7;
    height: 500px;
    padding-bottom: 80px;
    text-align: center;
}

.heading {
    color: #5f5d60;
    padding-bottom: 30px;
    padding-top: 60px;
    h2 {
        font-weight: normal;
    }
}

.lesson {
    float: left;
    width: 25%;
    .lesson-icon {
        position: relative;
        p {
            color: #fff;
            position: absolute;
            top: 85px;
            width: 100%
        }
    }
    .txt-contents {
        color: #b3aeb5;
        display: inline-block;
        font-size: 15px;
        margin-top: 20px;
        width: 80%;
    }
}

//message styles

.message-wrapper {
    border-bottom: 1px solid #eee;
    padding-bottom: 80px;
    text-align: center;
    .heading {
        h3 {
            font-weight: normal;
        }
        .message {
            background-color: #5dca88;
            box-shadow: 0 7px #1a7940;
            cursor: pointer;
            padding: 15px 40px;
            &:active {
                box-shadow: none;
                position: relative;
                top: 7px;
            }
        }
    }
}

//footer styles
footer {
    padding-top: 30px;
    img {
        width: 125px;
    }
    p {
        color: #b3aeb5;
        font-size: 12px;
    }
}